const Translation = ({ doStuff, setInput, result }) => {
  console.log({ result });
  return (
    <div className="text">
      <textarea
        onChange={(e) => setInput(e.target.value)}
        className="text-area"
        cols={55}
        rows={10}
      ></textarea>
      <button onClick={doStuff} className="action-btn">
        Do Your stuff
      </button>
      <div className="result-text">
        {result.length !== null ? result : "Write anything and submit"}
      </div>
    </div>
  );
};

export default Translation;
